.container3_channel{
    border-bottom: 3px solid rgba(1, 26, 96, 0.692);
    padding: 0.5rem;
    color: whitesmoke;
    position: relative;
}
.channel_logo_channel{
    border: 10px solid rgba(49, 7, 219, 0.66);
    width: 5rem;
    height: 5rem;
    border-radius: 100%;
    display: flex;
    margin: auto;
    background-color: rgba(92,135,4,0.769);
}
.channel_logo_channel b{
    margin:  auto;
    font-size: 3rem;
}
.description_channel{
    text-align: center;
    /* margin-left: 10rem; */
    
}
.description_channel b{
    font-size: 4rem;
}
.description_channel p{
    font-size: large;
}
.uploadbtn_channel,.editbtn_channel{
    position: absolute;
   /* margin-left: 4rem; */
    right:  1rem;
    top: 0.5rem;
    background-color: blue;
    padding: 0.5rem;
    border: 2px solid transparent;
    border-radius: 0.5rem;
    color: white;
    transition: all 1s;
    cursor: pointer;
}
.uploadbtn_channel{
    top:4rem;
}
.uploadbtn_channel:hover,.editbtn_channel:hover{
    border: 2px solid;
    color: rgb(231,140,14);
}